let userId = '',
    password = '';
import LongButton from '../../components/my-components/LongButton';
import PopUp from '../../components/my-components/PopUp.vue';
export default {
    components: {
        LongButton,
        PopUp
    },
    computed: {
        canSub() {
            return this.newName === '' || this.question === '请选择安全提示问题' || this.answer === '';
        }
    },
    data() {
        return {
            openEye: false,
            questionList: [
                '您的母亲叫什么名字？',
                '您配偶的生日是？',
                '您的学号（或工号）是？',
                '您的高中班主任的名字是？',
                '您的父亲的生日是？',
                '您最熟悉的学校宿舍室友的名字是？',
                '您的小学老师叫什么名字？'
            ],
            checkedNo: '',
            showList: false,
            newName: '',
            answer: '',
            question: '请选择安全提示问题'
        }
    },
    methods: {
        select(val, index) {
            this.checkedNo = index;
            this.question = val;
        },
        showQuestions() {
            this.showList = true;
        },
        closeQuestions() {
            this.showList = false;
        },
        nextPage() {
            uni.reLaunch({
                url: '../../pages/index/index'
            })
        },
        getName(val) {
            if (val.type == 'input') {
                this.newName = val.detail;
            } else {
                this.newName = '';
            }
        },
        getAnswer(val) {
            if(val.type=='input'){
                this.answer = val.detail;
            }else{
                 this.answer ='';
            }
            
        }
    },
}
